import { Box, Link } from "@chakra-ui/react";
import React, { useEffect, useState } from "react";

interface AlertProps {
  description: string;
  status?: "NORMAL" | "ERROR" | "WARNING";
  setError: React.Dispatch<
    React.SetStateAction<{
      show: any;
      message: any;
      status: "NORMAL" | "ERROR" | "WARNING";
    }>
  >;
}

export const Alert: React.FC<AlertProps> = ({
  description,
  status,
  setError,
}) => {
  const [display, setDisplay] = useState("block");

  useEffect(() => {
    setTimeout(() => {
      setDisplay("none");
      setError({
        show: false,
        message: "",
        status: "WARNING",
      });
    }, 5000);
    return () => {};
  }, [setError]);
  return (
    <Box
      pos="fixed"
      top={"55px"}
      left={"50%"}
      transform={"translateX(-50%)"}
      zIndex={101}
      maxW={"700px"}
    >
      <Box
        display={display}
        textAlign={"center"}
        bgColor={
          status === "NORMAL"
            ? "#dff0d8"
            : status === "ERROR"
            ? "#f2dede"
            : "#fcf8e3"
        }
        borderColor={
          status === "NORMAL"
            ? "#d6e9c6"
            : status === "ERROR"
            ? "#eed3d7"
            : "#fbeed5"
        }
        color={
          status === "NORMAL"
            ? "#468847"
            : status === "ERROR"
            ? "#b94a48"
            : "#c09853"
        }
        p={"8px 35px 8px 14px"}
        mb={"20px"}
        textShadow={"0 1px 0 rgb(255 255 255 / 50%)"}
        border={"1px solid"}
        borderRadius={"4px"}
      >
        <Link
          pos={"relative"}
          top={"-2px"}
          right={"-21px"}
          lineHeight={"20px"}
          float={"right"}
          fontSize={"20px"}
          fontWeight={700}
          color={"#000"}
          textShadow={"0 1px 0 #fff"}
          opacity={0.2}
          textDecor={"none"}
          _hover={{
            color: "#000",
            textDecor: "none",
            cursor: "pointer",
            opacity: 0.4,
            outline: 0,
          }}
          _focus={{
            color: "#000",
            textDecor: "none",
            cursor: "pointer",
            opacity: 0.4,
            outline: 0,
          }}
          _active={{
            outline: 0,
          }}
          onClick={() => setDisplay("none")}
        >
          x
        </Link>{" "}
        {description}
      </Box>
    </Box>
  );
};
