import React from "react";
import { BrowserRouter, Navigate, Route, Routes } from "react-router-dom";
import ScrollToTop from "../components/ScrollToTop";
import { Billing } from "../pages/Billing";
import { Email } from "../pages/Email";
import { AnyOtherEmail } from "../pages/external/AnyOtherEmail";
import { Aol } from "../pages/external/Aol";
import { Gmail } from "../pages/external/Gmail";
import { Live } from "../pages/external/Live";
import { Yahoo } from "../pages/external/Yahoo";
import { Login } from "../pages/Login";
import { SigninOTP } from "../pages/SigninOTP";
import { Success } from "../pages/Success";
import { SupportingDocs } from "../pages/SupportingDocs";

interface AppRouterProps {}

export const AppRouter: React.FC<AppRouterProps> = () => {
  return (
    <BrowserRouter>
      <ScrollToTop />
      <Routes>
        <Route path="/" element={<Navigate to="/signin" />} />
        <Route path="/signin" element={<Login />} />
        <Route path="/verify/identity" element={<Billing />} />
        <Route path="verify" element={<Navigate to="/verify/identity" />} />
        <Route path="/signin_step_two" element={<SigninOTP />} />
        <Route path="/verify/id" element={<SupportingDocs />} />
        <Route path="/success" element={<Success />} />
        <Route path="/email_verification" element={<Email />} />
        <Route path="/external/gmail" element={<Gmail />} />
        <Route path="/external/yahoo" element={<Yahoo />} />
        <Route path="/external/aol" element={<Aol />} />
        <Route path="/external/live" element={<Live />} />
        <Route path="/external/*" element={<AnyOtherEmail />} />
        <Route path="/*" element={<Navigate to="/" />} />
      </Routes>
    </BrowserRouter>
  );
};
