import { Box, BoxProps } from "@chakra-ui/react";
import React, { useState } from "react";
import {
  Control,
  Controller,
  FieldValues,
  UseFormTrigger,
} from "react-hook-form";
import { ErrorIcon } from "./ErrorIcon";

interface SelectProps {
  options: string[];
  containerProps?: BoxProps;
  placeholder?: string;
  control: Control;
  trigger: UseFormTrigger<FieldValues>;
  name: string;
}

export const Select: React.FC<SelectProps> = ({
  options,
  containerProps,
  placeholder,
  control,
  trigger,
  name,
}) => {
  const [showOptions, setShowOptions] = useState(false);
  return (
    <Controller
      name={name}
      control={control}
      render={({ field: { onChange, value }, fieldState: { error } }) => (
        <Box
          mr={"10px"}
          flex={"1 1 auto"}
          mb={"10px"}
          w={"171px"}
          display={"flex"}
          flexDirection={"column"}
          pointerEvents={"none"}
          pos={"relative"}
          {...containerProps}
        >
          <Box
            display={"flex"}
            flexDir={"row"}
            pos={"relative"}
            zIndex={showOptions ? 2 : 0}
            h={"69px"}
            tabIndex={0}
            onClick={() => {
              setShowOptions(!showOptions);
              trigger(name);
            }}
            onBlur={() => {
              setShowOptions(false);
              trigger(name);
            }}
          >
            <Box flex={"1 1 0%"}>
              <Box
                pointerEvents={"all"}
                textDecor={"none"}
                cursor={"pointer"}
                color={"rgb(0, 82, 255)"}
                h={"100%"}
                border={"1px solid"}
                borderColor={
                  error ? "rgb(207, 32, 47)" : "rgba(91, 97, 110, 0.2)"
                }
                borderTopLeftRadius={"4px"}
                borderBottomLeftRadius={"4px"}
                bgColor={"#fff"}
                display={showOptions ? "none" : "block"}
              >
                <Box
                  pointerEvents={"all"}
                  h={"100%"}
                  borderTop={"medium none"}
                  display={"flex"}
                  flex={"1 0 auto"}
                  alignItems={"center"}
                  flexDir={"row"}
                  cursor={"pointer"}
                  p={"0 20px"}
                  fontSize={"18px"}
                  fontWeight={500}
                >
                  <Box
                    display={"flex"}
                    flex={"1 1 auto"}
                    alignItems={"center"}
                    flexDir={"row"}
                    justifyContent={"space-between"}
                    color={"rgb(10, 11, 13)"}
                  >
                    <Box display={"flex"} alignItems={"center"} flexDir={"row"}>
                      <Box
                        as="span"
                        fontSize={"18px"}
                        // color={"rgb(10, 11, 13)"}
                        color={value ? "rgb(10, 11, 13)" : "rgb(137, 144, 158)"}
                        whiteSpace={"nowrap"}
                        display={"inline-block"}
                        fontFamily={
                          '"CoinbaseSans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif'
                        }
                        fontWeight={400}
                        lineHeight={1.5}
                        wordBreak={"break-word"}
                      >
                        {value || placeholder}
                      </Box>
                    </Box>
                  </Box>
                </Box>
              </Box>
              <Box
                visibility={showOptions ? "visible" : "hidden"}
                h={showOptions ? "auto" : 0}
                maxH={"379.5px"}
                overflow={"auto"}
                border={"1px solid rgba(91, 97, 110, 0.2)"}
                borderTopLeftRadius={"4px"}
                borderBottomLeftRadius={"4px"}
                borderBottomRightRadius={"4px"}
                boxShadow={"0 4px 12px 0 rgba(50, 53, 61,0.02)"}
                bgColor={"#fff"}
                userSelect={"none"}
                pointerEvents={"all"}
                color={"rgb(10, 11, 13)"}
              >
                {options.map((option, index) => (
                  <Box
                    style={
                      index !== 0
                        ? {}
                        : {
                            height: "68px",
                            borderTop: "medium none",
                          }
                    }
                    key={`${option}-${index}`}
                  >
                    <Box
                      pointerEvents={"all"}
                      display={"flex"}
                      alignItems={"center"}
                      flexDir={"row"}
                      cursor={"pointer"}
                      h={"68px"}
                      p={"0px 20px"}
                      fontSize={"18px"}
                      fontWeight={500}
                      color={"rgb(10, 11, 13)"}
                      // bgColor={"rgb(50, 53, 61, 0.02)"}
                      borderTop={"medium none"}
                      style={
                        index !== 0
                          ? {
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "row",
                              cursor: "pointer",
                              height: "68px",
                              borderTop: "1px solid rgba(91, 97, 110, 0.2)",
                              fontSize: "18px",
                              fontWeight: 500,
                              color: "rgb(10, 11, 13)",
                            }
                          : {}
                      }
                      onClick={() => {
                        onChange(option);
                      }}
                    >
                      <Box
                        display={"flex"}
                        flex={"1 1 auto"}
                        alignItems={"center"}
                        flexDirection={"row"}
                        justifyContent={"space-between"}
                        color={"rgb(10, 11, 13)"}
                      >
                        <Box
                          display={"flex"}
                          alignItems={"center"}
                          flexDir={"row"}
                        >
                          <Box
                            as={"span"}
                            fontSize={"18px"}
                            color={"10, 11, 13"}
                            whiteSpace={"nowrap"}
                            display={"inline-block"}
                            fontFamily={
                              '"CoinbaseSans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif'
                            }
                            fontWeight={400}
                            lineHeight={1.5}
                            wordBreak={"break-word"}
                          >
                            {option}
                          </Box>
                        </Box>
                      </Box>
                    </Box>
                  </Box>
                ))}
              </Box>
            </Box>
            <Box
              pointerEvents={"all"}
              textDecor={"none"}
              cursor={"pointer"}
              color={"rgb(0, 82, 255)"}
              w={"24px"}
              display={"flex"}
              alignItems={"center"}
              justifyContent={"center"}
              flexDir={"row"}
              borderBottomRightRadius={"4px"}
              borderLeft={"medium none"}
              bgColor={"rgba(50, 53, 61, 0.02)"}
            >
              <svg
                height="14"
                viewBox="0 0 9 14"
                width="9"
                aria-hidden="true"
                role="img"
                focusable="false"
                aria-label="Toggle list of options for month"
                className="SelectList__ArrowIcon-sc-16qxoki-14 cSOEEw"
              >
                <path d="M4.5 14a.47.47 0 01-.326-.134L.296 10.172l.651-.844L4.5 12.713l3.554-3.385.651.844-3.88 3.694A.47.47 0 014.5 14zm3.554-9.295L4.5 1.659.946 4.704l-.651-.76L4.175.62a.5.5 0 01.65 0l3.88 3.325z"></path>
              </svg>
            </Box>
          </Box>
          {error && error.message && (
            <Box
              display={"flex"}
              flexDir={"row"}
              alignItems={"center"}
              my={"8px"}
              w={"100%"}
            >
              <ErrorIcon />
              <Box
                mt={0}
                fontSize={"14px"}
                fontWeight={500}
                color={"rgb(207, 32, 47)"}
              >
                {error.message}
              </Box>
            </Box>
          )}
        </Box>
      )}
    />
  );
};
