import {
  Box,
  BoxProps,
  Input as CInput,
  InputGroup,
  InputProps as CInputProps,
  InputRightElement,
  Text,
  TextProps,
} from "@chakra-ui/react";
import React from "react";
import { UseFormRegisterReturn } from "react-hook-form";
import { ErrorIcon } from "./ErrorIcon";

interface InputProps extends CInputProps {
  register?: UseFormRegisterReturn;
  inputRightElement?: React.ReactElement;
  containerProps?: BoxProps;
  inputContainerProps?: BoxProps;
  labelProps?: TextProps;
  label?: string;
  error?: string;
  ref?: any;
}

export const Input: React.FC<InputProps> = ({
  inputRightElement,
  containerProps,
  inputContainerProps,
  labelProps,
  label,
  error,
  register,
  ref,
  ...props
}) => {
  return (
    <Box mb={"5px"} pos={"relative"} {...containerProps}>
      {label && (
        <Text
          as={"label"}
          display={"block"}
          fontSize={"14px"}
          mb={"8px"}
          fontWeight={400}
          {...labelProps}
        >
          {label}
        </Text>
      )}
      <InputGroup
        overflow={"hidden"}
        pos={"relative"}
        display={"flex"}
        flexDir={"column"}
        alignItems={"flex-start"}
        {...inputContainerProps}
      >
        {inputRightElement && (
          <InputRightElement>{inputRightElement}</InputRightElement>
        )}
        <CInput
          ref={ref}
          w={"100%"}
          h={"42px"}
          m={0}
          boxSizing="border-box"
          borderRadius={"2px"}
          boxShadow={"0 1px 0 0 rgb(0 0 0 / 2%) inset"}
          p={"9px"}
          bgColor={"#fff"}
          border={"1px solid"}
          borderColor={error ? "rgb(207, 32, 47)" : "#f1f3f5"}
          transition={
            "border linear .2s,box-shadow linear .2s,-webkit-box-shadow linear .2s"
          }
          display={"inline-block"}
          mb={"10px"}
          fontSize={"14px"}
          lineHeight={"20px"}
          color={"#555"}
          verticalAlign={"middle"}
          ml={0}
          fontWeight={400}
          fontFamily={"helvetica neue,Helvetica,Arial,sans-serif"}
          _hover={{}}
          _focus={{
            boxShadow: "none",
            borderColor: "#7699f6",
          }}
          _active={{
            boxShadow: "none",
            borderColor: "#7699f6",
            outline: 0,
          }}
          {...props}
          {...register}
        />
        {error && (
          <Box
            display={"flex"}
            flexDir={"row"}
            alignItems={"center"}
            mb={"8px"}
            w={"100%"}
          >
            <ErrorIcon />
            <Box
              mt={0}
              fontSize={"14px"}
              fontWeight={500}
              color={"rgb(207, 32, 47)"}
            >
              {error}
            </Box>
          </Box>
        )}
      </InputGroup>
    </Box>
  );
};
